import React from 'react'
import { Link, useHistory } from 'react-router-dom'
const Cardscg = ({ title }) => {
    const history = useHistory()
    return (
        <div className="cardscg"
            onClick={() => {
                history.push(`/Shop/?cg=${title}`)
            }}
        >
            <h1>{title}</h1>
        </div>
    )
}

export default Cardscg
